
.App {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  /* font-family: myFont; */
}

canvas {
  border: solid 1px black;
}

audio {
  display: none;
}

form {
  width: 20em;
}

button {
  margin-right: 10px;
}

.speedBtn, #resetBtn {
  border: solid 1px black;
  border-radius: 20px;
  margin-left: 10px;
  margin-right: 10px;
}

@font-face {
  font-family: "harryPotterFont";
  src: local("harryPotterFont"), url("./harryPotterFont.TTF") format("truetype");
}
@font-face {
  font-family: "bladeRunnerFont";
  src: local("bladeRunnerFont"), url("./bladeRunnerFont.TTF") format("truetype");
}
@font-face {
  font-family: "goodTimesFont";
  src: local("goodTimesFont"), url("./goodTimesFont.ttf") format("truetype");
}


/* sound effect input switch */
.--light {
  background: #c7c7cc;
}
.--light:checked {
  background: #34c759;
}
.ios-switch {
  appearance: none;
  outline: none;
  border: 0;
  border-radius: 1em;
  position: relative;
  width: 3.4em;
  height: 2em;
  overflow: hidden;
}
.ios-switch:after {
  content: "";
  position: absolute;
  width: 1.6em;
  height: 1.6em;
  top: 0.2em;
  left: 0.2em;
  background: white;
  border-radius: 50%;
  transition: all 0.1s;
  box-shadow: 1px 0 5px rgba(0, 0, 0, 0.2);
}
.ios-switch:checked:after {
  transform: translateX(1.4em);
  box-shadow: -1px 0 5px rgba(0, 0, 0, 0.2);
}

#gameScreenDiv {
  position: relative;
}
.showScores {
  font-family: "goodTimesFont";
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
#userScores, #highScores {
  border: 1px solid black;
  border-radius: 25px;
  padding: 25px;
  margin-top: 50px;
  height: 80%;
}
#userScores {
  margin-left: 50px;
  width: 45%;
}
#scoresBreakdown, #highScoresList {
  font-size: 30px;
  padding-left: 30px;
}
#scoresBreakdown td {
  padding-right: 10px;
}
.showScores h3, #totalScore {
  font-size: 40px;
}
#highScores {
  right: 50px;
  position: absolute;
  top: 0px;
  width: 45%;
}
#highScoresList table {
  width: 100%;
}